import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { Helmet } from 'react-helmet-async';
import {
    Alert,
    Box,
    Breadcrumbs,
    Card,
    CardContent,
    Container,
    Link,
    Typography,
    styled,
    Snackbar
} from '@mui/material';
import { useAuth } from 'src/contexts/AuthContext';
import { projectService } from 'src/services/projectService';
import ProjectForm from './components/ProjectForm';

const RootWrapper = styled(Box)(({ theme }) => ({
    display: "flex",
    width: '100%',
    flexDirection: 'column',
    height: `calc(100dvh - ${theme.header.mobileHeight})`,
    [theme.breakpoints.up("md")]: {
        height: `calc(100dvh - ${theme.header.height})`
    }
}));


const ProjectAdd = () => {
    const navigate = useNavigate();

    const [snackbar, setSnackbar] = useState({
        open: false,
        message: '',
        severity: 'success'
    });

    const handleCreateProject = async (projectData) => {
        try {
            await projectService.createProject(projectData);
            setSnackbar({
                open: true,
                message: 'Project created successfully!',
                severity: 'success'
            });
            setTimeout(() => {
                navigate('/projects');
            }, 1500);
        } catch (error) {
            console.error('Error creating project:', error);
            setSnackbar({
                open: true,
                message: 'Failed to create project',
                severity: 'error'
            });
        }
    };

    const handleCancel = () => {
        navigate('/projects');
    };

    const handleSnackbarClose = () => {
        setSnackbar({ ...snackbar, open: false });
    };

    return (
        <>
            <Helmet>
                <title>Create Project | OneDollarAI</title>
            </Helmet>
            <RootWrapper sx={{ overflowY: 'auto', pb: 2, scrollbarColor: '#888 #e3e3e3' }}>
                <Container maxWidth="lg">
                    <Box sx={{ mt: 3, mb: 4 }}>
                        {/* Breadcrumbs */}
                        <Breadcrumbs sx={{ mb: 2 }}>
                            <Link
                                color="inherit"
                                href="/projects"
                                onClick={(e) => {
                                    e.preventDefault();
                                    navigate('/projects');
                                }}
                                sx={{ cursor: 'pointer', color: 'black' }}
                            >
                                Projects
                            </Link>
                            <Typography color="black">Create Project</Typography>
                        </Breadcrumbs>
                        <Typography variant="h2" component="h1" gutterBottom color="black">
                            Create Project
                        </Typography>
                        <Typography variant="body1" color="black">
                            Fill in the details to create a new project.
                        </Typography>
                    </Box>
                    <Card>
                        <CardContent>
                            <ProjectForm
                                onSubmit={handleCreateProject}
                                onCancel={handleCancel}
                            />
                        </CardContent>
                    </Card>
                </Container>
            </RootWrapper>
            <Snackbar
                open={snackbar.open}
                autoHideDuration={6000}
                onClose={handleSnackbarClose}
            >
                <Alert
                    onClose={handleSnackbarClose}
                    severity={snackbar.severity}
                    sx={{ width: '100%' }}
                >
                    {snackbar.message}
                </Alert>
            </Snackbar>
        </>
    );
};

export default ProjectAdd; 